.hero-slider {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100vw;
  height: 100vh;
  z-index: 0;
  /* background: #f5f5f5; */
  /* box-shadow: 0 30px 50px #dbdbdb; */
  overflow-x: hidden;
}


.hero-track {
  border-radius: 0px;
}

.slide-card {
  width: 0px;
  height: 0px;
  position: absolute;
  justify-content: space-between;
  top: 85%;

  margin-right: 20px;
  transform: translate(0, -50%);
  border-radius: 50%;
  /* box-shadow: 0 30px 50px #1900ff; */
  background-position: 50% 50%;
  background-size: cover;
  display: flex;
  transition: all 0.5s;
}

.hero-track .slide-card:nth-child(1),
.hero-track .slide-card:nth-child(2) {
  top: 0;

  transform: translate(0, 0);
  border-radius: 0;
  width: 100%;
  height: 100%;
  transition: all .5s;
}

.hero-track .slide-card:nth-child(n+3) {
  opacity: 0;
  pointer-events: none;
}

