.slide-info {
  z-index: 10;
  position: absolute;
  top: 30%;
  left: 100px;
  width: 300px;
  text-align: left;
  color: #eee;
  transform: translate(0, -50%);
  font-family: system-ui;
  display: none;
}

.slide-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  width: 100%;
  height: 100%;
  background: linear-gradient(149deg, #022c4dde 0%, #ff7b0000 100%);


}

.hero-track .slide-card:nth-child(2) .slide-info,
.slide-info.active {
  display: block;
}

.slide-title {

  z-index: 1000;
  font-size: 40px;
  text-transform: uppercase;
  font-weight: bold;
  opacity: 0;
  /* background-color: black; */
  animation: animate 1s ease-in-out 1 forwards;
}

.slide-desc {
  margin-top: 10px;
  margin-bottom: 10px;
  opacity: 1;
  animation: animate 1s ease-in-out 0.3s 1 forwards;
}

.read-more-btn {
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  opacity: 0;
  border-radius: 3px;
  background-color: rgba(255, 255, 255, 0.673);
  transition: all 0.5s;
  animation: animate 1s ease-in-out 0.6s 1 forwards;
}

.read-more-btn:hover {
  background-color: #fff;
}

@keyframes animate {
  from {
    opacity: 0;
    transform: translate(0, 100px);
    filter: blur(33px);
  }

  to {
    opacity: 1;
    transform: translate(0);
    filter: blur(0);
  }
}

