@media screen and (max-width: 768px) {
  .slide-card {
    width: 150px;
    height: 200px;
  }

  .hero-track .slide-card:nth-child(3) {
    left: 50%;
  }

  .hero-track .slide-card:nth-child(4) {
    left: calc(50% + 170px);
  }

  .hero-track .slide-card:nth-child(5) {
    left: calc(50% + 340px);
  }

  .hero-track .slide-card:nth-child(n+6) {
    left: calc(50% + 510px);
    opacity: 0;
  }

  .slide-info {
    left: 30px;
    width: 250px;
  }

  .slide-title {
    font-size: 28px;
  }

  .slide-desc {
    font-size: 13px;
  }

  .read-more-btn {
    padding: 8px 16px;
    font-size: 13px;
  }

  .slider-buttons {
    bottom: 20px;
    gap: 15px;
  }

  .slide-btn {
    width: 42px;
    height: 42px;
  }
}

@media screen and (max-width: 480px) {
  .slide-card {
    width: 120px;
    height: 160px;
  }

  .hero-track .slide-card:nth-child(3) {
    left: 50%;
  }

  .hero-track .slide-card:nth-child(4) {
    left: calc(50% + 120px);
  }

  .hero-track .slide-card:nth-child(5) {
    left: calc(50% + 280px);
  }

  .hero-track .slide-card:nth-child(n+6) {
    left: calc(50% + 420px);
    opacity: 0;
  }

  .slide-info {
    left: 20px;
    width: 200px;
  }

  .slide-title {
    font-size: 22px;
  }

  .slide-desc {
    font-size: 11px;
    margin-top: 8px;
    margin-bottom: 12px;
  }

  .read-more-btn {
    padding: 6px 12px;
    font-size: 12px;
  }

  .slider-buttons {
    bottom: 15px;
    gap: 12px;
  }

  .slide-btn {
    width: 38px;
    height: 38px;
    font-size: 12px;
  }
}