.Careers-section {
    margin-top: 50px;
    /* Set a fixed height for the section */
    /* height: 500px; */
    /* Placeholder for your actual image */
    background-image: url('https://images.pexels.com/photos/1000445/pexels-photo-1000445.webp?cs=srgb&dl=pexels-belle-co-99483-1000445.webp&fm=webp');
    background-size: cover;
    background-position: center;
    position: relative;
    /* Adds a subtle overall dimming to the entire section for better text contrast against the lighter background areas */
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.1);
}

.Careers-text-area {
    position: absolute;
    /* top: 50%; */
    /* Create the dark, semi-transparent overlay just for the text area */
    background: linear-gradient(90deg, rgb(0, 0, 0) 0%, rgb(0, 0, 0) 50%, rgba(0, 92, 150, 0) 100%);
    padding: 60px;

    height: 100%;
    /* To ensure the left edge is clean and the text area is visible */
    z-index: 1;
}

.Careers_btn {
    background: transparent;
    color: #ffffff;
    width: 150px;
    border: 1px solid #ffffff;
    border-radius: 50px;
    padding: 10px 20px;
}

.Careers_btn:hover {
    background: #ffffff;
    color: #000000;
    transition: 0.5s;
}

/* Ensure the Bootstrap container and row take up full height */
.hero-section>.container-fluid,
.hero-section>.container-fluid>.row {
    height: 100%;
}


.careers_card {
    height: 500px;
    /* border: 1px solid #000000; */
    color: #fff;
}

.careers_card_img img {
    width: 500px;
    height: auto;
    object-fit: cover;
}