body {
    font-family: "Nunito Sans", sans-serif;
    font-optical-sizing: auto;
}


.about-us {
    background-color: #ffffff;
}

.about-us h1 {
    color: var(--text-dark);
    letter-spacing: -0.5px;
}

.about-us .lead {
    line-height: 1.8;
    font-size: 1.05rem;
    max-width: 800px;
}

.info-block {
    padding: 0;
}

.info-block h3 {
    color: var(--text-dark);
    font-size: 1.1rem;
    font-weight: 600;
    letter-spacing: -0.3px;
}

.info-block p {
    font-size: 0.95rem;
    line-height: 1.6;
    color: var(--text-muted);
}

/* Responsive Design */
@media (max-width: 991px) {
    .about-us .row {
        flex-direction: column;
    }

    .col-lg-3,
    .col-lg-4 {
        width: 100% !important;
    }

    .about-us h1 {
        font-size: 2rem;
    }
}

@media (max-width: 576px) {
    .about-us {
        padding: 2rem 0;
    }

    .about-us h1 {
        font-size: 1.75rem;
        margin-bottom: 1.5rem;
    }

    .about-us .lead {
        font-size: 1rem;
    }

    .info-block {
        margin-bottom: 2rem;
    }
}

.headline {
    font-family: "Nunito Sans", sans-serif;
    font-size: 2.75rem;
    font-weight: 700;
    line-height: 1.2;
    color: #000000;
    margin: 0;
}

@media (max-width: 1024px) {
    .headline {
        font-size: 2rem;
    }
}

@media (max-width: 768px) {
    .headline {
        font-size: 1.5rem;
    }
}

@media (max-width: 480px) {
    .headline {
        font-size: 1.5rem;
    }
}

.subheadline {
    color: #e94e40;
    font-weight: 700;
}

.stats {
    color: #fff;
    font-size: 1.2rem;
}

.stat-value {
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    color: #000000;
}

.stat-value_text {
    color: #161616;
}

.card-custom {
    background: #232323;
    border: none;
    color: #fff;
}

.divider {
    width: 8px;
    height: 60px;
    background: linear-gradient(180deg, #05243c 60%, #2ea1ff 100%);
    display: inline-block;
    vertical-align: middle;
    margin-right: 18px;
}

.profile-img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid #232323;
}

.videoplay-btn {
    background: none;
    border: 2px solid #e94e40;
    color: #e94e40;
    border-radius: 50%;
    padding: 10px 14px;
    font-size: 1.6rem;
    cursor: pointer;
}

/* Image Slider Styles */
.img-slider {
    display: grid;
    place-items: center;
    grid-template-areas: "stack";
}

/* @media (max-width: 1023px) {
    .img-slider img {
        height: 450px !important;
        width: 100% !important;
    }
} */

.img-slider>* {
    grid-area: stack;
}

.slider-image {
    opacity: 0;
    transition: opacity 1s ease-in-out;
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 8px;
    /* kept for consistency */
}

.slider-image.active {
    opacity: 1;
    z-index: 1;
}

@media (max-width: 1024px) {
    .description2 {
        font-size: 1.25rem !important;
        /* width: 100% !important; */
    }
}

/* TABLET ONLY MEDIA QUERY (iPad, Tablets) */
/* Generally between 768px and 1024px */
@media screen and (min-width: 768px) and (max-width: 1024px) {







    /* Adjust image slider column width or display */
    .img-slider {
        height: auto;
        /* Allow height to adjust based on image */
        margin-bottom: 2rem;
        /* Add spacing below image */
        width: 100%;
    }

    .img-slider img {
        height: auto;
        width: 100%;
        object-fit: cover;
    }

    /* Adjust the description text size and alignment */
    .description2 {
        font-size: 1.25rem !important;
        text-align: justify;
        width: 100% !important;
        /* align-items: right; */
    }

    /* Adjust stats section layout */
    .stats {
        /* margin-top: 2rem; */
        align-items: center;
        z-index: 100 !important;
        width: 100%;
        /* top: 0 !important;
        left: 0 !important; */
        /* transform: translate(-50%, -50%); */
    }

    .stat-value {
        font-size: 1.8rem;
    }

    .stat-value_text {
        font-size: 0.9rem;
    }

    /* Adjust columns layout if needed */

    /* Hide specific content on tablets */
    .tablet-hidden {
        display: none;
    }

}

/* MOBILE DEVICES MEDIA QUERY (Smartphones) */
/* Generally less than 768px */
@media screen and (max-width: 767px) {

    /* Modify the container padding if needed */


    /* Adjust image slider column width or display */
    .img-slider {
        height: auto !important;
        width: 100% !important;
        /* Reverting to container width for visibility check */
        position: relative;
        /* Ensure grid display is maintained for stacking */
        display: grid;
        place-items: center;
        z-index: 1;
        /* Ensure it's above background but below text if needed */
        opacity: 1 !important;
        transform: none !important;
        transition: none !important;
        pointer-events: auto !important;
        margin-bottom: 2rem;
    }

    .img-slider img {
        height: 100% !important;
        width: 100% !important;
        max-width: none !important;
        object-fit: cover !important;
    }

    /* Adjust the description text size and alignment */
    .description2 {
        font-size: 1rem !important;
        text-align: justify;
    }

    /* Adjust stats section layout */
    .stats {
        /* margin-top: 1.5rem; */
        align-items: center;
        z-index: 100 !important;
        width: 100%;
    }

    .stat-value {
        font-size: 1.5rem;
    }

    .stat-value_text {
        font-size: 0.8rem;
    }

    /* Use the same class name from tablets for hidden content on mobile too */
    .tablet-hidden {
        display: none;
    }
}