        .header-section {
            background: linear-gradient(135deg, var(--primary-blue) 0%, #2c5282 100%);
            color: white;
            padding: 60px 0;
        }

        .breadcrumb-custom {
            background: transparent;
            padding: 0;
            margin-bottom: 20px;
        }

        .breadcrumb-custom .breadcrumb-item a {
            color: rgba(255, 255, 255, 0.8);
            text-decoration: none;
        }

        .breadcrumb-custom .breadcrumb-item.active {
            color: white;
        }

        .job-header {
            background: white;
            border-radius: 15px;
            padding: 40px;
            margin-top: -50px;
            position: relative;
            z-index: 2;
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);
        }

        .job-title {
            color: var(--primary-blue);
            font-weight: 700;
            margin-bottom: 15px;
        }

        .job-meta {
            display: flex;
            flex-wrap: wrap;
            gap: 20px;
            margin-bottom: 20px;
        }

        .job-meta-item {
            display: flex;
            align-items: center;
            color: var(--dark-gray);
            font-size: 0.9rem;
        }

        .job-meta-item i {
            color: var(--accent-orange);
            margin-right: 8px;
            width: 16px;
        }

        .section-title {
            color: var(--primary-blue);
            font-weight: 600;
            margin-bottom: 1.5rem;
            padding-bottom: 10px;
            border-bottom: 2px solid var(--accent-orange);
            display: inline-block;
        }

        .content-section {
            padding: 60px 0;
        }

        .sidebar-card {
            background: white;
            border-radius: 15px;
            padding: 30px;
            box-shadow: 0 5px 20px rgba(0,0,0,0.08);
            margin-bottom: 30px;
            border-left: 4px solid var(--accent-orange);
        }

        .sidebar-card h5 {
            color: var(--primary-blue);
            font-weight: 600;
            margin-bottom: 20px;
        }

        .btn-primary-custom {
            background-color: var(--accent-orange);
            border-color: var(--accent-orange);
            padding: 15px 40px;
            font-weight: 600;
            border-radius: 25px;
            transition: all 0.3s ease;
            text-transform: uppercase;
            letter-spacing: 0.5px;
        }

        .btn-primary-custom:hover {
            background-color: #e55a2b;
            border-color: #e55a2b;
            transform: translateY(-2px);
            box-shadow: 0 5px 15px rgba(255, 107, 53, 0.3);
        }

        .btn-outline-custom {
            color: var(--primary-blue);
            border-color: var(--primary-blue);
            padding: 12px 30px;
            font-weight: 600;
            border-radius: 25px;
            transition: all 0.3s ease;
        }

        .btn-outline-custom:hover {
            background-color: var(--primary-blue);
            border-color: var(--primary-blue);
            color: white;
        }

        .requirements-list {
            list-style: none;
            padding: 0;
        }

        .requirements-list li {
            padding: 10px 0;
            border-bottom: 1px solid #eee;
            position: relative;
            padding-left: 30px;
        }

        .requirements-list li:before {
            content: '\f00c';
            font-family: 'Font Awesome 6 Free';
            font-weight: 900;
            color: var(--accent-orange);
            position: absolute;
            left: 0;
            top: 12px;
             color: #ff6836;
        }

        .responsibilities-list {
            list-style: none;
            padding: 0;
        }

        .responsibilities-list li {
            padding: 12px 0;
            position: relative;
            padding-left: 30px;
            border-bottom: 1px solid #f0f0f0;
        }

        .responsibilities-list li:before {
            content: '\f0da';
            font-family: 'Font Awesome 6 Free';
            font-weight: 900;
            color: var(--accent-orange);
            position: absolute;
            left: 0;
            top: 14px;
            color: #ff6836;
        }

        .benefits-grid {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
            gap: 20px;
            margin-top: 20px;
        }

        .benefit-item {
            background: var(--light-gray);
            padding: 20px;
            border-radius: 10px;
            text-align: center;
            transition: transform 0.3s ease;
        }

        .benefit-item:hover {
            transform: translateY(-3px);
        }

        .benefit-item i {
            color: var(--accent-orange);
            font-size: 2rem;
            margin-bottom: 15px;
        }

        .benefit-item h6 {
            color: var(--primary-blue);
            font-weight: 600;
            margin-bottom: 10px;
        }

        .similar-jobs-section {
            background-color: var(--light-gray);
            padding: 60px 0;
        }

        .job-card {
            background: white;
            border-radius: 10px;
            padding: 25px;
            margin-bottom: 20px;
            box-shadow: 0 3px 15px rgba(0,0,0,0.08);
            transition: transform 0.3s ease;
            border-left: 4px solid var(--accent-orange);
        }

        .job-card:hover {
            transform: translateY(-3px);
        }

        .job-card h6 {
            color: var(--primary-blue);
            font-weight: 600;
            margin-bottom: 10px;
        }

        .job-card .job-meta-small {
            font-size: 0.85rem;
            color: var(--dark-gray);
            margin-bottom: 15px;
        }

        .company-info-section {
            background: var(--primary-blue);
            color: white;
            padding: 60px 0;
        }

        .company-stats {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
            gap: 30px;
            text-align: center;
        }

        .stat-number {
            font-size: 2.5rem;
            font-weight: 700;
            color: var(--accent-orange);
            display: block;
        }

        .sticky-sidebar {
            position: sticky;
            top: 120px;
        }

        @media (max-width: 768px) {
            .job-meta {
                flex-direction: column;
                gap: 10px;
            }
            
            .job-header {
                padding: 25px;
                margin-top: -30px;
            }
            
            .benefits-grid {
                grid-template-columns: 1fr;
            }
        }