.contact-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 24px;
  max-width: 1200px;
  margin: 50px auto;
}

.contact-card {
  position: relative;
  background: linear-gradient(155deg, #ffffff 0%, rgba(253, 29, 29, 0) 50%, #ececec 100%);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border-radius: 30px;
  /* border: 1px solid rgba(255, 255, 255, 0.459); */
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.075),
    inset 0 1px 0 rgba(255, 255, 255, 0.13),
    inset 0 -1px 0 rgba(255, 255, 255, 0.034),
    inset 0 0 14px 7px rgba(243, 243, 243, 0.041);
  position: relative;
  overflow: hidden;
  padding: 40px 30px;

  display: flex;
  height: 400px;
  flex-direction: column;
  align-items: flex-start;
}

