.contact-card.tall {
  grid-row: span 2;
  height: 500px;
}

.card-icon {
  color: #0066cc;
  font-size: 32px;
  margin-bottom: 20px;
}

.card-label {
  font-size: 14px;
  color: #666;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 12px;
  font-weight: 600;
}

.card-text {
  font-size: 16px;
  color: #333;
  font-weight: 500;
  line-height: 1.6;
}

.middle-column {
  display: grid;
  gap: 24px;
}

@media (max-width: 992px) {
  .contact-container {
    grid-template-columns: 1fr 1fr;
  }

  .contact-card.tall {
    grid-row: span 1;
  }

  .middle-column {
    grid-template-rows: auto auto;
  }
}

@media (max-width: 576px) {
  .contact-container {
    grid-template-columns: 1fr;
  }
}