body {
  font-family: "Nunito Sans", sans-serif !important;
  font-optical-sizing: auto;
}


.App {
  min-height: 80vh;
  background: linear-gradient(90deg, rgba(5, 36, 60, 1) 0%, rgb(12, 24, 37) 50%);
  font-family: 'Inter', sans-serif;
  color: #ffffff;
}

.our-story-section {
  max-width: 1400px;
  margin: 0 auto;
  padding: 80px 40px;
}

/* Content Wrapper - Two Column Layout */
.content-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 80px;
  margin-bottom: 30px;
  align-items: center;
}

/* Left Column Styles */
.left-column {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.section-label {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 8px;
}

.label-text {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 2px;
  color: #ffffff;
  text-transform: uppercase;
}

.label-line {
  width: 60px;
  height: 2px;
  background: linear-gradient(90deg, #ffffff 0%, transparent 100%);
}

.main-headline {
  font-family: "Nunito Sans", sans-serif;
  font-size: 3.75rem;
  font-weight: 700;
  line-height: 1.2;
  color: #fff;
  margin: 0;
  animation: fadeIn 0.5s ease;
}

.main-headline .highlight {
  font-family: "Nunito Sans", sans-serif;
  background: -webkit-linear-gradient(90deg, #00f7ff 0%, #00f7ff 50%, #00f7ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.description {
  font-size: 1.125rem;
  line-height: 1.7;
  color: #f0f0f0;
  max-width: 540px;
  animation: fadeIn 0.5s ease 0.1s both;
}

.description2 {
  text-align: justify;
  font-size: 1.125rem;
  line-height: 1.7;
  color: #030303;
  animation: fadeIn 0.5s ease 0.1s both;
}


@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.know-more-btn {
  background: #00f7ff;
  color: rgb(0, 0, 0);
  border: none;
  padding: 16px 40px;
  border-radius: 50px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  align-self: flex-start;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.know-more-btn:hover {
  transform: translateY(-2px);
  /* box-shadow: 0 8px 24px rgba(239, 68, 68, 0.4); */
}

.know-more-btn:active {
  transform: translateY(0);
}

/* Right Column - Image */
.right-column {
  position: relative;
}

.image-container {
  position: relative;
  width: 100%;
  height: 500px;
  border-radius: 24px;
  overflow: hidden;
  margin-bottom: 0px;
}

.main-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: grayscale(100%) contrast(1.1);
  border-radius: 24px;
  animation: fadeIn 0.5s ease;
}

.get-in-touch-circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 140px;
  height: 140px;
  background: linear-gradient(155deg, rgba(131, 58, 180, 1) 0%, rgba(253, 29, 29, 1) 50%, rgba(252, 176, 69, 1) 100%);
  border-radius: 50%;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.get-in-touch-circle:hover {
  transform: translate(-50%, -50%) scale(1.05);
  box-shadow: 0 8px 32px #00f7ff;
}

.circle-text {
  position: absolute;
  width: 100%;
  height: 100%;
  animation: rotate 15s linear infinite;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.circle-label {
  fill: white;
  font-size: 9px;
  font-weight: 600;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.arrow-icon {
  color: white;
  z-index: 1;
}

/* Timeline Section */
.timeline-section {
  position: relative;
  display: flex;
  align-items: center;
  gap: 20px;
  padding: 40px 0;
  /* height: 10px;
  width: 100%; */
}

.timeline-nav-btn {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: white;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  flex-shrink: 0;
  transition: background 0.2s ease, border-color 0.2s ease;
}

.timeline-nav-btn:hover {
  background: rgba(239, 68, 68, 0.2);
  border-color: #ef4444;
}

.timeline-container {
  position: relative;
  display: flex;
  gap: 80px;
  overflow-x: auto;
  scroll-behavior: smooth;
  padding: 40px 20px 20px;
  flex: 1;
  scrollbar-width: none;
  -ms-overflow-style: none;
  height: max-content;
  width: 100%;
}

.timeline-container::-webkit-scrollbar {
  display: none;
}

.timeline-line {
  position: absolute;
  top: 50px;
  left: 20px;
  right: 20px;
  height: 2px;
  background: linear-gradient(90deg,
      rgba(255, 255, 255, 0.1) 0%,
      rgba(239, 68, 68, 0.3) 50%,
      rgba(255, 255, 255, 0.1) 100%);
  z-index: 0;
  padding: 0%;
}

.timeline-item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  flex-shrink: 0;
  z-index: 1;
  transition: transform 0.3s ease;
}

.timeline-item:hover {
  transform: translateY(-4px);
}

.timeline-dot {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 3px solid rgba(255, 255, 255, 0.4);
  background: transparent;
  margin-bottom: 16px;
  transition: all 0.3s ease;
  position: relative;
  z-index: 2;
}

.timeline-item.active .timeline-dot {
  background: #00f7ff;
  border-color: #ffae0000;
  box-shadow: 0 0 20px rgba(239, 68, 68, 0.6);
}

.timeline-year {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.5);
  transition: color 0.3s ease;
  white-space: nowrap;
}

.timeline-item.active .timeline-year {
  color: #ffffff;
}

.timeline-details {
  position: absolute;
  top: 100%;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  animation: fadeInUp 0.4s ease;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.timeline-thumbnail {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid #ef4444;
  box-shadow: 0 4px 16px rgba(239, 68, 68, 0.3);
}

.timeline-title {
  font-size: 0.95rem;
  font-weight: 600;
  color: #ffffff;
  text-align: center;
  max-width: 180px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .our-story-section {
    padding: 60px 30px;
  }

  .content-wrapper {
    gap: 60px;
    margin-bottom: 50px;
  }

  .main-headline {
    font-size: 2.5rem;
  }

  .image-container {
    height: 420px;
  }
}

@media (max-width: 768px) {
  .our-story-section {
    padding: 40px 20px;
  }

  .content-wrapper {
    grid-template-columns: 1fr;
    gap: 40px;
    margin-bottom: 60px;
  }

  .main-headline {
    font-size: 2rem;
  }

  .description {
    font-size: 1rem;
  }

  .image-container {
    height: 350px;
  }

  .get-in-touch-circle {
    width: 110px;
    height: 110px;
  }

  .circle-label {
    font-size: 7px;
  }

  .timeline-section {
    gap: 12px;
    /* padding: 30px 0; */
  }

  .timeline-nav-btn {
    width: 40px;
    height: 40px;
  }

  .timeline-container {
    gap: 60px;
    padding: 30px 10px 20px;
  }

  .timeline-year {
    font-size: 1.25rem;
  }

  .timeline-thumbnail {
    width: 60px;
    height: 60px;
  }

  .timeline-title {
    font-size: 0.85rem;
    max-width: 150px;
  }

  .timeline-line {
    top: 40px;
  }
}

@media (max-width: 480px) {
  .our-story-section {
    padding: 30px 16px;
  }

  .main-headline {
    font-size: 1.5rem;
  }

  .know-more-btn {
    padding: 14px 32px;
    font-size: 14px;
    width: 100%;
  }

  .image-container {
    height: 280px;
  }

  .get-in-touch-circle {
    width: 90px;
    height: 90px;
  }

  .timeline-container {
    gap: 50px;
  }

  .timeline-details {
    margin-top: 16px;
  }

  .timeline-thumbnail {
    width: 50px;
    height: 50px;
  }

  .timeline-title {
    font-size: 0.75rem;
    max-width: 120px;
  }
}


.img_slider_section {
  padding: 50px;
  width: 100%;
  height: 10vh;
  position: relative;
}