/* Main Container */
.imgcontainer {
  margin-top: 90px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  /* 4 columns on desktop */
  grid-auto-rows: 300px;
  /* Fixed row height, allows auto-placement */
  grid-auto-flow: dense;
  /* Fills gaps automatically */
  /* gap: 10px; */
  /* Optional: Adds spacing between images */
  padding: 10px;
}

/* Box Styling */
.imgcontainer .box {
  position: relative;
  background: #f0f0f0;
  /* Placeholder background */
  overflow: hidden;
  /* border-radius: 8px; */
  /* Optional: Rounded corners */
}

.imgcontainer .box img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.imgcontainer .box:hover img {
  transform: scale(1.05);
}

/* 
  Type Definitions:
  a: Portrait (Vertical) -> Spans 2 Rows
  b: Landscape (Horizontal) -> Spans 2 Columns
  c: Square (Standard) -> Spans 1 Row, 1 Column (Default)
*/

.imgcontainer .box.a {
  grid-row: span 2;
}

.imgcontainer .box.b {
  grid-column: span 2;
}

/* Media Queries for Responsiveness */

/* Tablet (max-width: 991px) -> 3 Columns */
@media (max-width: 991px) {
  .imgcontainer {
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 250px;
  }
}

/* Mobile (max-width: 768px) -> 2 Columns */
@media (max-width: 768px) {
  .imgcontainer {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 200px;
    /* gap: 5px; */
    padding: 5px;
  }
}

