/* General Body and Container Styles */
body {
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  /* linqWhite */
  color: #000000;
  /* black */
  overflow-x: hidden;
  /* Prevent horizontal scroll from AOS or other elements */
}

.container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem;
  padding-right: 1rem;
}

.colorText {
  text-transform: uppercase;
  background: linear-gradient(to right, #006ea1 20%, #020063 100%);
  /* -webkit-background-clip: text; */
  -webkit-text-fill-color: transparent;
}

.navbar_linq {
  backdrop-filter: blur(4px);
  background: linear-gradient(180deg, #05243ce6 0%, rgba(5, 36, 60, 0) 100%) !important;
  filter: url(#navbarNav);
  /* background-color: #062a46b4; */
  position: fixed;
  width: 100%;
  z-index: 1000;
  top: 0;
}

.navbar_linq ul {
  font-size: 20px !important;
}

.navbar_linq li :hover {
  color: #00e1ff !important;

}

.navbar_linq img {
  width: 100px;
}

@media (max-width: 991px) {
  .navbar-collapse {
    background-color: #05243c;
    /* Dark background for mobile menu */
    padding: 1rem;
    border-radius: 0 0 10px 10px;
  }
}


@media (min-width: 1024px) {

  /* lg:order-last lg:aspect-square */
  .hero-image {
    order: 1;
    /* This is for lg:order-last, but in a 2-column grid, it means the image is on the right */
    aspect-ratio: 1 / 1;
  }
}

:root {
  --linq-dark: #05243c;
  --linq-white: #ffffff;
  --linq-black: #000000;
}

.bg-linq-dark {
  background: linear-gradient(90deg, rgba(5, 36, 60, 1) 0%, rgba(36, 59, 85, 1) 50%);
}

.bg-linq-Grey {
  background-color: #fcfcfc;
}

.text-linq-dark {
  color: var(--linq-dark) !important;
}

.bg-linq-white {
  background-color: var(--linq-white) !important;
}

.text-linq-white {
  color: var(--linq-white) !important;
}

.bg-linq-black {
  background-color: var(--linq-black) !important;
}

.text-linq-black {
  color: var(--linq-black) !important;
}

.btn-linq-primary {
  background-color: var(--linq-dark);
  color: var(--linq-white);
  border-color: var(--linq-white);
}

.btn-linq-primary:hover {
  background-color: var(--linq-white);
  /* Placeholder for darker shade */
  color: var(--linq-black);
  border-color: darken(var(--linq-dark), 10%);
}

.btn-outline-linq-dark {
  color: var(--linq-dark);
  border-color: var(--linq-dark);
}

.btn-outline-linq-dark:hover {
  background-color: var(--linq-dark);
  color: var(--linq-white);
}

/* Custom card styles for sections */
.linq-card {
  background-color: var(--linq-white);
  color: var(--linq-black);
  border: none;
  border-radius: 0.5rem;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.linq-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}

/* Specific styles for hero section cards */
.hero-linq-card {
  background-color: rgba(255, 255, 255, 0.068);
  /* border-top: 1px solid rgba(255, 255, 255, 0); */
  border: none;
  color: var(--linq-white);
  backdrop-filter: blur(4px);
  transition: 0.5s;
}

.hero-linq-card:hover {
  background: linear-gradient(148deg, rgba(122, 220, 255, 0) 0%, rgba(0, 195, 255, 0.212) 50%, rgba(32, 14, 112, 0) 100%);
  transform: scale(1.05);
  box-shadow: none;
  /* Remove shadow on hover for this specific card */
}

/* Image hover effect */
.img-hover-zoom {
  transition: transform 0.5s ease-in-out;
}

.img-hover-zoom:hover {
  transform: scale(1.05);
}

/* New styles for Life at LINQ features - Alternating layout */
.linq-feature-block {
  display: flex;
  height: 140px;
  align-items: center;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  /* Space between blocks */
  position: relative;
  overflow: hidden;
  background-color: rgba(255, 255, 255, 0.08);
  /* Subtle background for each block */
  border-radius: 0.75rem;
  transition: background-color 0.3s ease-in-out;
}

.linq-feature-block:hover {
  background-color: rgba(255, 255, 255, 0.15);
  /* Lighten on hover */
}

.linq-feature-block:last-child {
  margin-bottom: 0;
}

.linq-feature-block .feature-icon-wrapper {
  flex-shrink: 0;
  width: 70px;
  /* Fixed size for icon */
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  /* Circular background for icon */
  background-color: rgba(255, 255, 255, 0.15);
  /* Slightly more prominent icon background */
  margin-right: 1.5rem;
  /* Space between icon and text */
  transition: transform 0.3s ease-in-out;
}

.linq-feature-block:hover .feature-icon-wrapper {
  transform: scale(1.1);
  /* Icon pop effect on hover */
}

.linq-feature-block .feature-icon {
  color: var(--linq-white);
}

.linq-feature-block .feature-content {
  flex-grow: 1;
}

.linq-feature-block .feature-title {
  font-size: 1.35rem;
  /* h5 equivalent */
  font-weight: 600;
  margin-bottom: 0.4rem;
}

.linq-feature-block .feature-description {
  font-size: 0.95rem;
  opacity: 0.85;
}

/* Alternating layout for larger screens */
@media (min-width: 992px) {

  /* lg breakpoint */
  .linq-feature-row {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
  }

  .linq-feature-row:nth-child(even) .linq-feature-block {
    margin-left: auto;
    /* Push even blocks to the right */
  }

  .linq-feature-row:nth-child(odd) .linq-feature-block {
    margin-right: auto;
    /* Push odd blocks to the left */
  }

  .linq-feature-row .col-lg-6 {
    display: flex;
    justify-content: center;
    /* Center content within its column */
  }
}


.Aboutus img {
  width: 100%;
  height: auto;
}


.lifeatlinq {
  width: 400px;
  height: 100%;
}

.lifeAtLinq img {
  width: 400px;
  height: 230px;
}


/* Add this to your styles.css */
html {
  scroll-padding-top: 80px;
  /* Adjust based on your navbar height */
  overflow-x: hidden;
}

/* For mobile responsiveness */
@media (max-width: 991px) {
  html {
    scroll-padding-top: 80px;
  }
}

@keyframes type {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

/* @keyframes cursor {
  50% {
    border-color: transparent;
  }
} */


.typeTaxt {
  color: #dbd66e00;
  background: -webkit-linear-gradient(25deg, #ffffff, #c2f3ff);
  -webkit-text-fill-color: transparent;
  font-size: 3rem;
  font-weight: 500;
  overflow: hidden;
  white-space: nowrap;
  border-right: 2px solid;
  animation: type 1.5s steps(90) 1.5s 1 normal both, cursor 1s step-end infinite;
  position: relative;
}






.pricing__section {
  margin-block: 3rem;
}

.wrapper {
  max-width: 1240px;

  margin-inline: auto;

  padding-inline: 1rem;
}

.title {
  text-align: center;

  display: grid;

  gap: 1rem;

  place-items: center;
}

.title h2 {
  font-size: 40px;
}

.toggle__section {
  display: flex;

  gap: 1rem;

  align-items: center;
}

.toggle__section input {
  display: none;
}

.toggle__section .toggle__btn {
  display: flex;

  padding: 4px;

  background: var(--accent);

  border-radius: 2rem;

  width: 2.35rem;
}

.toggle__section .toggle__btn span {
  display: block;

  width: 1rem;

  height: 1rem;

  background: var(--primary);

  border-radius: 50%;
}

input:checked~.toggle__btn {
  justify-content: end;
}

.Careers__cards {
  margin-block: 2rem;

  display: grid;

  gap: 2rem;

  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

.Careers__card {
  /* background-image: url(''); */
  background-color: #f3f3f3;
  padding: 2rem 1.5rem;
  box-shadow: 2px 0 24px #f7f7f7;
  border-radius: 1rem;
  display: inline-flex;
  gap: 1rem;
  flex-direction: column;
}

.Careers__details {
  flex: 1 1 auto;
}

.Careers__details h3 {
  font-size: 32px;

  line-height: 1.5;
}

.Careers__details ul {
  margin-block: 1rem;
}

.Careers__details ul li::before {
  content: ">";

  margin-right: 8px;

  font-weight: bold;
}

hr {
  border: none;

  height: 0;

  border-bottom: 1px solid #ddd;
}

.Careers__card .Careers {
  font-size: 32px;

  font-weight: 600;

  letter-spacing: -2px;
}

.Careers__card .Careers:before {
  content: "$";
}



.bg__accent {
  background: var(--accent);

  color: var(--primary);
}

.bg__accent-2 {
  background-color: #f3f3f3;

  color: var(--accent);
}

.card-img-top {
  width: 100%;
  height: 300px;
  object-fit: cover;
}