/* =====================================================
   LINQ HERO SECTION - IMAGES IN BACKGROUND
   ===================================================== */

/* Main Hero Section */
.linq_hero {
    position: relative;
    min-height: 100%;
    width: 100%;
    /* overflow: hidden; */
    display: flex;
    align-items: center;
    background-image: url("../images/Hero-img/1.webp");
    background-size: cover;
    background-position: center;
    z-index: 1;
}

.LINQ_Name {
    color: white;
    font-size: 4rem;
    font-weight: bold;
    text-align: center;
    z-index: 2;
}

@media (max-width: 768px) {
    .LINQ_Name {
        font-size: 2rem;
        /* Smaller font for mobile */
    }
}

@media (min-width: 769px) and (max-width: 1024px) {
    .LINQ_Name {
        font-size: 3rem;
        /* Tablet size */
    }
}

/* type js style  */


/* Background Slider - Behind Everything */
.linq-hero-slider-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 0;
}

.linq-hero-slider-bg img {
    width: 100%;
    height: auto;
    object-fit: cover;
    object-position: center;
}

/* Mobile Hero Image - Hidden by default on desktop */
.mobile-hero-img {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 0;
}

/* Tablet Hero Image - Hidden by default */
.tablet-hero-img {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 0;
}

/* Slick Slider Container Override */
.linq-hero-slider-bg .slick-slider,
.linq-hero-slider-bg .slick-list,
.linq-hero-slider-bg .slick-track {
    height: 100%;
}

.linq-hero-slider-bg .slick-slide {
    opacity: 0;
    transition: opacity 1s ease-in-out;
}

.linq-hero-slider-bg .slick-slide.slick-active {
    opacity: 1;
}

/* Remove Slick Navigation */
.linq-hero-slider-bg .slick-dots,
.linq-hero-slider-bg .slick-prev,
.linq-hero-slider-bg .slick-next {
    display: none !important;
}

/* Dark Overlay for Text Readability */
.linq-hero-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(5, 36, 60, 0.76);
    z-index: 1;
}

/* Content Positioned on Top */
.linq-hero-content {
    position: relative;
    z-index: 2;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
}

/* Hero Content Alignment */
.hero-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
}

/* Enhanced Card Styling */
.hero-card {
    background-color: rgba(255, 255, 255, 0.08);
    border: 1px solid rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(10px);
    transition: all 0.3s ease;
}

.hero-card:hover {
    background-color: rgba(255, 255, 255, 0.12);
    border-color: rgba(255, 255, 255, 0.25);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
    transform: translateY(-5px);
}

/* Responsive Design */
@media (max-width: 600px) {
    .linq_hero {
        min-height: 100vh;
        height: auto;
    }

    /* Hide Slider on Mobile */
    .linq-hero-slider-bg {
        display: none !important;
    }

    /* Show Mobile Image on Mobile */
    .mobile-hero-img {
        display: block !important;
    }

    .heroContant {
        margin-top: 20% !important;
        padding-bottom: 2rem;
    }

    .hero-title {
        font-size: 1.5rem !important;
        line-height: 1.2;
    }

    .hero-cards-grid {
        grid-template-columns: 1fr !important;
        gap: 1rem;
    }

    .hero-card {
        padding: 0.75rem !important;
    }

    .hero-card-title {
        font-size: 1rem !important;
    }

    .hero-card-description {
        font-size: 0.75rem !important;
    }

    .hero-card-icon {
        width: 1.5rem !important;
        height: 1.5rem !important;
    }
}

@media (min-width: 601px) and (max-width: 1023px) {
    .linq_hero {
        min-height: 90vh;
        /* Changed to min-height */
        height: auto;
    }

    /* Hide Slider on Tablet */
    .linq-hero-slider-bg {
        display: none !important;
    }

    /* Hide Mobile Image on Tablet */
    .mobile-hero-img {
        display: none !important;
    }

    /* Show Tablet Image on Tablet */
    .tablet-hero-img {
        display: block !important;
    }

    .heroContant {
        margin-top: 10%;
    }

    .hero-title {
        font-size: 2.5rem;
    }

    .hero-cards-grid {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (min-width: 1024px) {
    .linq_hero {
        height: 100vh;
    }


    .hero-title {
        font-size: 3.75rem;
    }

    .hero-cards-grid {
        grid-template-columns: repeat(3, 1fr);
    }

    /* Ensure images cover full height on desktop/large screens (>1024px) */
    .linq-hero-slider-bg {
        position: absolute;
        /* Reinforce absolute position */
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        height: 100% !important;
        width: 100% !important;
    }

    .linq-hero-slider-bg .slick-slider,
    .linq-hero-slider-bg .slick-list,
    .linq-hero-slider-bg .slick-track,
    .linq-hero-slider-bg .slick-slide,
    .linq-hero-slider-bg .slick-slide>div {
        height: 100% !important;
    }

    .linq-hero-slider-bg img {
        height: 100% !important;
        object-fit: cover;
    }
}