.careers-animated-list-container {
    width: 100%;
    padding: 1rem 0;
}

.careers-animated-list {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

.careers-list-item {
    opacity: 0;
    transform: translateX(-30px) scale(0.95);
    transition: none;
}

.careers-list-item.visible {
    animation: slideInFromLeft 0.6s cubic-bezier(0.34, 1.56, 0.64, 1) forwards;
}

@keyframes slideInFromLeft {
    0% {
        opacity: 0;
        transform: translateX(-30px) scale(0.95);
    }

    60% {
        transform: translateX(5px) scale(1.02);
    }

    100% {
        opacity: 1;
        transform: translateX(0) scale(1);
    }
}

.careers-list-card {
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.95) 0%, rgba(255, 255, 255, 0.98) 100%);
    border: 1px solid rgba(0, 0, 0, 0.08);
    border-radius: 2px;
    height: 60px;
    padding: 1.25rem;
    position: relative;
    overflow: hidden;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    /* box-shadow: 0 2px 8px rgba(90, 90, 90, 0.04); */
}

.careers-list-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    color: #000;
    background: linear-gradient(90deg,#d6f7ff 0%, rgb(244, 255, 193) 50%, #d6f7ff 100%);
    opacity: 0;
    transition: opacity 0.3s ease;
    pointer-events: none;
}

.careers-list-card:hover {
    transform: translateY(-4px) translateX(4px);
    box-shadow: 0 12px 24px rgba(90, 90, 90, 0.041);
    /* border-color: rgba(59, 130, 246, 0.3); */
}

.careers-list-card:hover::before {
    opacity: 1;
}

.careers-card-glow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* background: radial-gradient(circle at top left, rgba(16, 63, 134, 0.651), transparent 60%); */
    opacity: 0;
    transition: opacity 0.3s ease;
    pointer-events: none;
}

.careers-list-card:hover .careers-card-glow {
    opacity: 1;
}

.careers-card-content {
    display: flex;
    align-items: flex-start;
    gap: 1rem;
    position: relative;
    z-index: 1;
}

.careers-icon-wrapper {
    flex-shrink: 0;
    /* width: 48px;
    height: 48px; */
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, #3b83f600 0%rgba(15, 24, 156, 0)75 100%);
    border-radius: 10px;
    transition: all 0.3s ease;
    /* box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3); */
}


.careers-list-card:hover .careers-icon-wrapper {
    transform: scale(1.1) rotate(5deg);
    /* box-shadow: 0 6px 16px rgba(59, 130, 246, 0.4); */
}

.careers-icon {
    color: #091E42;
    transition: transform 0.3s ease;
}

.careers-list-card:hover .careers-icon {
    transform: scale(1.1);
}

.careers-text-content {
    flex: 1;
    min-width: 0;
}

.careers-item-title {
    font-size: 1.1rem;
    font-weight: 700;
    color: #1e293b;
    margin: 0 0 0.5rem 0;
    line-height: 1.3;
    transition: color 0.3s ease;
}

.careers-list-card:hover .careers-item-title {
    color: #181818;
}

.careers-item-description {
    font-size: 0.9rem;
    color: #64748b;
    line-height: 1.6;
    margin: 0 0 0.5rem 0;
}

.careers-item-timestamp {
    display: inline-block;
    font-size: 0.75rem;
    font-weight: 600;
    color: #8b5cf6;
    background: rgba(139, 92, 246, 0.1);
    padding: 0.25rem 0.75rem;
    border-radius: 12px;
    margin-top: 0.25rem;
    transition: all 0.3s ease;
}

.careers-list-card:hover .careers-item-timestamp {
    background: rgba(139, 92, 246, 0.2);
    color: #7c3aed;
}

/* Responsive Design */
@media (max-width: 768px) {
    .careers-list-card {
        padding: 1rem;
    }

    .careers-card-content {
        gap: 0.75rem;
    }

    .careers-icon-wrapper {
        width: 40px;
        height: 40px;
    }

    .careers-icon {
        width: 20px;
        height: 20px;
    }

    .careers-item-title {
        font-size: 1rem;
    }

    .careers-item-description {
        font-size: 0.85rem;
    }
}

/* Dark mode support for careers section */
[data-theme="dark"] .careers-list-card,
.bg-linq-dark .careers-list-card {
    background: linear-gradient(135deg, rgba(30, 41, 59, 0.95) 0%, rgba(15, 23, 42, 0.98) 100%);
    border-color: rgba(255, 255, 255, 0.1);
}

[data-theme="dark"] .careers-list-card:hover,
.bg-linq-dark .careers-list-card:hover {
    border-color: rgba(59, 130, 246, 0.5);
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.4);
}

[data-theme="dark"] .careers-item-title,
.bg-linq-dark .careers-item-title {
    color: #f1f5f9;
}

[data-theme="dark"] .careers-list-card:hover .careers-item-title,
.bg-linq-dark .careers-list-card:hover .careers-item-title {
    color: #60a5fa;
}

[data-theme="dark"] .careers-item-description,
.bg-linq-dark .careers-item-description {
    color: #94a3b8;
}

/* Animation performance optimization */
@media (prefers-reduced-motion: reduce) {
    .careers-list-item.visible {
        animation: none;
        opacity: 1;
        transform: none;
    }

    .careers-list-card,
    .careers-icon-wrapper,
    .careers-icon,
    .careers-item-title,
    .careers-item-timestamp {
        transition: none;
    }
}