body {
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
}








.event-card {

  width: 332px;
  margin: 5px 0;
  margin: 10px;

  height: 550px;
  position: relative;
  overflow: hidden;
  border-radius: 0px;

}


.card-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.event-image {
  width: 100%;
  height: 100%;
  object-fit: cover;

}

.card-overlay1 {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 100%;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  background: linear-gradient(212deg, rgba(255, 255, 255, 0.041) 0%, #05243c 100%);
}

.card-overlay2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);

  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #05243c 100%);
}

.card-overlay3 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);

  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #05243c 100%);
}

.card-overlay4 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);

  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #05243c 100%);
}



.top-ui {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;

}


.bottom-content {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 10;

}

.card-title {
  font-size: 27px;

  line-height: 1;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.5);

}

.card-detail {
  font-size: 16px;
  font-weight: 500;
  color: rgba(255,
      255,
      255,
      0.7) !important;

}


.avatars {

  display: flex;
  justify-content: center;
}

.avatar-img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 3px solid white;

  object-fit: cover;
  background-color: lightgrey;

}

.avatar-img.shifted {

  margin-left: -10px;
}

.Discover_Life_at_Linq {
  text-align: center;
  background-color: #05243c00;
  color: #05243c;
  border: 1px solid #05243c;
  font-size: 16px;
  font-weight: 600;
  padding: 15px 30px;
  border-radius: 30px;
  transition: all 0.5s ease;
}

.Discover_Life_at_Linq:hover {

  background-color: #05243c;
  color: #ffffff;
  border: none;


}

.Discover_Life_at_Linq a {
  text-decoration: none;
}

@media screen and (max-width: 767px) {

  .event-card-wrapper {
    margin-left: 0px !important;
    margin-right: 15px !important;
  }
}